.user-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;
}

.user-item a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: white;
  transition: all 300ms;
}

.user-item a:hover,
.user-item a:active {
  /* background: #78ffd6; */
  /* background-image: linear-gradient(to bottom right, #bc4e9c , #f80759); */
  background: linear-gradient(to left, #a8ff78, #78ffd6);
}

.card.user-item__content {
  padding: 0 !important;
}

.user-item__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.user-item__info h2 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: 600;
  color: #78ffd6;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #fff;
}

.user-item__info h3 {
  margin: 0;
  color: #000;
}
